
.mint-toast {
    position: fixed;
    max-width: 80%;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    z-index: 1000;
    -webkit-transition: opacity .3s linear;
    transition: opacity .3s linear
}
.mint-toast.is-placebottom {
    bottom: 50px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0)
}
.mint-toast.is-placemiddle {
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}
.mint-toast.is-placetop {
    top: 50px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0)
}
.mint-toast-icon {
    display: block;
    text-align: center;
    font-size: 56px
}
.mint-toast-text {
    font-size: 14px;
    display: block;
    text-align: center
}
.mint-toast-pop-enter, .mint-toast-pop-leave-active {
    opacity: 0
}

.mint-indicator {
  -webkit-transition: opacity .2s linear;
  transition: opacity .2s linear;
}
.mint-indicator-wrapper {
  top: 50%;
  left: 50%;
  position: fixed;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
}
.mint-indicator-text {
  display: block;
  color: #fff;
  text-align: center;
  margin-top: 10px;
  font-size: 16px;
}
.mint-indicator-spin {
  display: inline-block;
  text-align: center;
}
.mint-indicator-mask {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: 0;
  background: transparent;
}
.mint-indicator-enter, .mint-indicator-leave-active {
  opacity: 0;
}

.mint-spinner-snake {
  -webkit-animation: mint-spinner-rotate 0.8s infinite linear;
          animation: mint-spinner-rotate 0.8s infinite linear;
  border: 4px solid transparent;
  border-radius: 50%;
}
@-webkit-keyframes mint-spinner-rotate {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes mint-spinner-rotate {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.mint-spinner-double-bounce {
position: relative;
}
.mint-spinner-double-bounce-bounce1, .mint-spinner-double-bounce-bounce2 {
width: 100%;
height: 100%;
border-radius: 50%;
opacity: 0.6;
position: absolute;
top: 0;
left: 0;
-webkit-animation: mint-spinner-double-bounce 2.0s infinite ease-in-out;
        animation: mint-spinner-double-bounce 2.0s infinite ease-in-out;
}
.mint-spinner-double-bounce-bounce2 {
-webkit-animation-delay: -1.0s;
        animation-delay: -1.0s;
}
@-webkit-keyframes mint-spinner-double-bounce {
0%, 100% {
    -webkit-transform: scale(0.0);
            transform: scale(0.0);
}
50% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
}
}
@keyframes mint-spinner-double-bounce {
0%, 100% {
    -webkit-transform: scale(0.0);
            transform: scale(0.0);
}
50% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
}
}
.mint-spinner-triple-bounce {}
.mint-spinner-triple-bounce-bounce1, .mint-spinner-triple-bounce-bounce2, .mint-spinner-triple-bounce-bounce3 {
border-radius: 100%;
display: inline-block;
-webkit-animation: mint-spinner-triple-bounce 1.4s infinite ease-in-out both;
        animation: mint-spinner-triple-bounce 1.4s infinite ease-in-out both;
}
.mint-spinner-triple-bounce-bounce1 {
-webkit-animation-delay: -0.32s;
        animation-delay: -0.32s;
}
.mint-spinner-triple-bounce-bounce2 {
-webkit-animation-delay: -0.16s;
        animation-delay: -0.16s;
}
@-webkit-keyframes mint-spinner-triple-bounce {
0%, 80%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
40% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
}
}
@keyframes mint-spinner-triple-bounce {
0%, 80%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
40% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
}
}
